import React, { Component, Suspense } from 'react';
import { Button, Dropdown, Accordion, Nav, Tab, ToggleButtonGroup, ToggleButton, OverlayTrigger, Tooltip } from 'react-bootstrap';
import EditWebEmbed from '../../EditWebEmbed'
import SVG from 'react-inlinesvg'
import { getAssetsCount } from '../../../../../Services/Actions/assetsActions'
import { baseUrl } from '../../../../../Assets/AssetsHelper';
class EmbedList extends Component {
    state = {
        active: null,
        activeAction: null
    }

    componentWillReceiveProps(nextProps) {
        const { state } = nextProps
        const { activeAction } = this.state
        if (state.collectionList.length > 0 && state.publishList.length > 0 && !activeAction) this.onUpdate(state.publishList[0], 0)
    }


    changeFilter = (key) => event => this.setState({
        activeAction: key
    })
    onClickToEdit = (item, key) => event => this.onUpdate(item, key)
    onUpdate = (item, key) => {
        var self = this
        this.setState({ activeAction: key })
        getAssetsCount(item.id).then((response) => {
            const { publishCode, assestCount } = response.data
            self.props.onUpdateReponseData({ assestCount: assestCount, publishCode: publishCode })
        }).catch((ex) => {
            self.props.onUpdateReponseData({ assestCount: 0, publishCode: item })
        })
    }
    render() {
        const { state, history, isListRequesting } = this.props
        const { activeAction } = this.state
        const counts = Array.from({ length: 8 }, (_, i) => i + 1);
        return <><div className='filter_list_wrapper' onChange={e => this.embedCodeCallback(e)}>
            <div className='filter_list'>
                {isListRequesting ? counts.map((count, index) => (
                    <div key={index} className="sk_rect h-54px mb-3">
                    </div>
                )) : state.collectionList.length > 0 && state.publishList.length > 0 ? state.publishList.map((item, pIndex) => {
                    return <div className={`d-flex rounded-1 mb-3 overflow-hidden border ${activeAction == pIndex ? 'border-dark' : 'bg-light border-light'}`} key={pIndex} onClick={this.onClickToEdit(item)}>
                        <div className="d-flex p-3 pe-0 cursor-pointer overflow-hidden flex-grow-1">
                            <span className="svg-icon svg-icon-2 flex-shrink-0 me-2 block-center d-flex text-gray-700">
                                <SVG src={baseUrl('media/icons/svg-icons/widget.svg')} height={32} width={32} />
                            </span>
                            <div className='d-flex flex-column flex-grow-1 overflow-hidden'>
                                <p className='fs-9 mb-0 fw-medium text-ellipsis'>{item.name}</p>
                                <span className='text-muted fs-9'>Wall :{item.wallId}</span>
                            </div>
                        </div>
                        <div className="block-center flex-shrink-0 p-3 ps-0">
                            <Button variant='icon' className="btn-active-light p-0 w-26px h-26px transition-none border-0" onClick={this.props.onClickToDelete({ "id": item.id })}>
                                <i className="ri-delete-bin-line icon-7"></i>
                            </Button>
                        </div>
                    </div>
                }) :
                    null}
            </div>
        </div>


        </>
    }

}
export default EmbedList


